<template>
    <h1>Action</h1>
    <div class="page">
    <table>
        <tr>
            <td>
                <a href="/actions">
                    <div class="data" v-if="this.AId == null">
                        <p style="font-size:x-large;">+</p>
                    </div>
                    <div class="data" v-if="this.AId == '6360ec0fedf314cd1be57068'">
                        <p style="font-size:x-large;">Discord</p>
                    </div>
                    <div class="data" v-if="this.AId == '63615dd6b44ce8272da128b5'">
                        <p style="font-size:x-large;">Github</p>
                    </div>
                    <div class="data" v-if="this.AId == '633856703a79d71704729438'">
                        <p style="font-size:x-large;">Gmail</p>
                    </div>
                    <div class="data" v-if="this.AId == '6341eed87b1999471f015cb7'">
                        <p style="font-size:x-large;">Time</p>
                    </div>
                    <div class="data" v-if="this.AId == '63555cb456586d005905995f'">
                        <p style="font-size:x-large;">Weather</p>
                    </div>
                </a>
            </td>
        </tr>
    </table>
    <h1>Reaction</h1>
    <p>Click on a reaction to remove it or on + to add one</p>
    <table>
        <tr v-for="R in REA" :key="R">
            <td>
                <a>
                    <div class="data">
                        <p>{{ R.name }}</p>
                    </div>
                </a>    
            </td>
        </tr>
        <tr>
            <td>
                <a href="/reactions">
                    <div class="data">
                        <p style="font-size:x-large;">+</p>
                    </div>
                </a>
            </td>
        </tr>
    </table>
        <button v-on:click="SendData">Confirm</button>
    </div>
</template>

<script>
export default {
    name: "RecapAREA",
    components: {
    },
    created() {
        this.AId = localStorage.actionId
        if (localStorage.reaction) {
            this.REA = JSON.parse(localStorage.reaction)
        }
    },
    data() {
        return {
            AId: null,
            REA: []
        }
    },
    methods: {
        SendData() {
            let url = 'http://localhost:8080/ar-user/'
            let res = {
                "action": localStorage.action,
                "reaction": localStorage.reaction
            }
            console.log(res)
            fetch(url, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded',
                    'token': localStorage.token,
                    "Accept": "text/html",
                },
                body: JSON.stringify(res),
                mode: 'cors'
            }
            )
                .then((res) => {
                    if (res.status === 201) {
                        res.text().then((ans) => {
                            console.log(ans)
                            this.$router.push({ name: "Dashboard" });
                        })
                    } else {
                        console.log(res)
                        alert("Something Went Wrong");
                    }
                })
                .catch((err) => {
                    console.log(err);
                });
        }
    }
}
</script>

